document.addEventListener('DOMContentLoaded', function(){
    eventListeners();
    darkMode();
    adminTablas();
    showPassword();
});

function darkMode(){

  const prefiereDarkMode = window.matchMedia('(prefers-color-scheme: dark');
  //Si tiene dark mode en el navegador
  if(prefiereDarkMode.matches){
      document.body.classList.add('.dark-mode');
  } else{
      document.body.classList.remove('.dark-mode');
  }

  prefiereDarkMode.addEventListener('change', function(){
      if(prefiereDarkMode.matches){
          document.body.classList.add('.dark-mode');
      } else{
          document.body.classList.remove('.dark-mode');
      }
  });

  const botonDarkMode = document.querySelector('.dark-mode-boton');

  botonDarkMode.addEventListener('click', function(){
      document.body.classList.toggle('dark-mode');
      botonDarkMode.classList.toggle('fa-moon');
      botonDarkMode.classList.toggle('fa-sun');

      if (document.body.classList.contains('dark-mode')) {
          localStorage.setItem('modo-oscuro','true');
      } else {
          localStorage.setItem('modo-oscuro','false');
      }

  });

  if (localStorage.getItem('modo-oscuro') === 'true') {
      document.body.classList.add('dark-mode');
      botonDarkMode.classList.remove('fa-moon');
      botonDarkMode.classList.add('fa-sun');
  } else {
      document.body.classList.remove('dark-mode');
      botonDarkMode.classList.remove('fa-sun');
      botonDarkMode.classList.add('fa-moon');   
  }
}

function eventListeners(){
    const mobileMenu = document.querySelector('.mobile-menu');  

    mobileMenu.addEventListener('click', navegacionResponsive);   
}

function navegacionResponsive(){
    const barra = document.querySelector('.barra');
    const navegacion = document.querySelector('.navegacion');
    const btnMenu = document.querySelector('.btnMenu');

    navegacion.classList.toggle('mostrar');
    barra.classList.toggle('no-borde');
    btnMenu.classList.toggle('fa-bars');
    btnMenu.classList.toggle('fa-xmark');
}

function adminTablas(){ 
    const iconoAdmin = document.querySelectorAll('.admin-icon');
 
    iconoAdmin.forEach(enlace => enlace.addEventListener('click', mostrarTablas));
 }
 
function mostrarTablas(e){
    let enlaces = e.target.id;
    let tabBtn = e.target;

    if(enlaces){
        let divAdmin = document.querySelector('#tabla-'+enlaces);
        let anterior = document.querySelector('.vertabla');
        anterior.classList.toggle('vertabla');
        divAdmin.classList.toggle('vertabla');

        let noActivo = document.querySelector('.tab-verde');
        noActivo.classList.toggle('tab-verde');
        tabBtn.classList.toggle('tab-verde');
    }
}
function showPassword(){
    let passbtn = document.querySelector('#passview');
    pass = document.querySelector('#password');
    passbtn.onclick = () => {
        if(passbtn.checked === true){
            pass.type = 'text'
        } else{
            pass.type = 'password'
        }        
    };
}

let slideIndex = 1;
showSlides(slideIndex);

function plusSlides(n) {
  showSlides(slideIndex += n);
}

function currentSlide(n) {
  showSlides(slideIndex = n);
}

function showSlides(n) {
  let i;
  let slides = document.getElementsByClassName("slide");
  let dots = document.getElementsByClassName("dot");
  if (n > slides.length) {slideIndex = 1}    
  if (n < 1) {slideIndex = slides.length}
  for (i = 0; i < slides.length; i++) {
    slides[i].style.display = "none";  
  }

  slides[slideIndex-1].style.display = "block";  
}

